@import "variables.scss";
@import "mixins.scss";

//
// Badges
// --------------------------------------------------


// Base class
.#{$class-prefix}badge {
  display: inline-block;
  min-width: 10px;
  padding: 3px 7px;
  font-size: $font-size-small;
  font-weight: $badge-font-weight;
  color: $badge-color;
  line-height: $badge-line-height;
  vertical-align: middle;
  white-space: nowrap;
  text-align: center;
  background-color: $badge-bg;
  border-radius: $badge-border-radius;

  // Empty badges collapse automatically (not available in IE8)
  &:empty {
    display: none;
  }

  // Quick fix for badges in buttons
  .#{$class-prefix}btn & {
    position: relative;
    top: -1px;
  }

  .#{$class-prefix}btn-xs &,
  .#{$class-prefix}btn-group-xs > .#{$class-prefix}btn & {
    top: 0;
    padding: 1px 5px;
  }

  // [converter] extracted a& to a.badge

  // Account for badges in navs
  .#{$class-prefix}list-group-item.#{$class-prefix}active > &,
  .#{$class-prefix}nav-pills > .#{$class-prefix}active > a > & {
    color: $badge-active-color;
    background-color: $badge-active-bg;
  }

  .#{$class-prefix}list-group-item > & {
    float: right;
  }

  .#{$class-prefix}list-group-item > & + & {
    margin-right: 5px;
  }

  .#{$class-prefix}nav-pills > li > a > & {
    margin-left: 3px;
  }
}

// Hover state, but only for links
a.#{$class-prefix}badge {
  &:hover,
  &:focus {
    color: $badge-link-hover-color;
    text-decoration: none;
    cursor: pointer;
  }
}
